ound-color: skyblue;\n}\ndiv.sidebar > button.active {\n  background-color: skyblue;\n}\ndiv.sidebar > button:nth-child(1) {\n  background-color: rgb(94, 158, 94);\n  height: 40px;\n  display: block;\n}\ndiv.sidebar > button > svg {\n  width: 20px;\n  position: absolute;\n  right: 10px;\n  top: calc(50% - 10px);\n  fill: darkred;\n}\ndiv.new-list-div {\n  overflow: hidden;\n  height: 40px;\n  background-color: transparent;\n  padding: 0;\n}\ndiv.new-list-div > input {\n  width: calc(100% - 20px);\n  padding-left: 10px;\n}\ndiv.new-list-div > input:focus {\n  outline: none;\n}\ndiv.new-list-div > button {\n  display: grid;\n  place-items: center;\n  font-size: large;\n  width: 30px;\n  background-color: rgb(94, 158, 94);\n  color: white;\n}\nmain {\n  grid-area: main;\n}\n/* To-do List Styles */\n.todo-list {\n  width: min(90%, 750px);\n  padding: 10px;\n  display: grid;\n  place-items: center;\n  margin: auto;\n}\n.todo-list > h2,\n.edit-title-input {\n  width: 100%;\n  margin-bottom: 20px;\n  background-color: cornflowerblue;\n  color: white;\n  padding: 10px;\n  height: 50px;\n  font-size: 25px;\n  font-weight: 600;\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n.edit-title-input {\n  display: none;\n}\n.edit-title-input:focus {\n  outline: none;\n}\n.todo-list > h2 > p {\n  font-size: small;\n  color: rgb(230, 230, 230);\n}\n.todo-item {\n  display: grid;\n  align-items: center;\n  outline: 1px solid;\n  padding: 10px;\n  width: 100%;\n  grid-template-columns: 30px 1fr 40px;\n  justify-content: space-between;\n  margin-bottom: 10px;\n  position: relative;\n  padding-left: 20px;\n}\n.todo-item > h3 {\n  line-height: 200%;\n}\n.todo-item > div.priority {\n  position: absolute;\n  width: 5px;\n  height: 100%;\n  outline: 1px solid black;\n}\n.todo-item[data-priority=\"low\"] > div.priority {\n  background-color: lightgreen;\n}\n.todo-item[data-priority=\"medium\"] > div.priority {\n  background-color: purple;\n}\n.todo-item[data-priority=\"high\"] > div.priority {\n  background-color: red;\n}\n.todo-item > div.checkbox {\n  display: grid;\n  place-content: center;\n  border: 1px solid black;\n  width: 10px;\n  height: 10px;\n  border-radius: 20px;\n  padding: 10px;\n}\n.todo-item > div.checkbox.checked::after {\n  content: \"\\2713\";\n  font-size: 20px;\n  background-color: transparent;\n}\n.todo-item:has(.checked) {\n  background-color: lightgray;\n}\n.todo-item:has(.checked) > h3 {\n  text-decoration: line-through;\n  text-decoration-thickness: 4px;\n}\n.todo-item:has(.checked) > div.priority {\n  background-color: black;\n}\n.todo-item > svg {\n  width: 35px;\n  justify-self: self-end;\n  fill: darkred;\n  cursor: pointer;\n}\n/* New Task Styles */\nbutton.new-list-item-button {\n  background-color: rgb(37, 151, 142);\n  color: white;\n  cursor: pointer;\n  margin-bottom: 10px;\n}\nbutton.new-list-item-button,\ndiv.new-list-item {\n  width: 100%;\n  height: 40px;\n  outline: 1px solid;\n}\ndiv.new-list-item {\n  display: grid;\n  grid-template-columns: 10fr 90fr 40px;\n}\ninput.new-list-item-input {\n  height: 100%;\n  padding: 10px;\n}\ninput.new-list-item-input:focus {\n  outline: none;\n}\ndiv.new-list-item > select {\n  height: 100%;\n  text-transform: uppercase;\n  background-color: rgb(37, 151, 142);\n  color: white;\n}\ndiv.new-list-item > select:focus {\n  outline: none;\n}\ndiv.new-list-item > svg {\n  flex: 1;\n  border-left: 1px solid white;\n  fill: white;\n  background-color: rgb(37, 151, 142);\n  cursor: pointer;\n  height: 100%;\n}\n/* Edit List Item Styles */\n.edit-list-item-backdrop {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.3);\n  display: grid;\n  place-items: center;\n}\nform.edit-list-item-form {\n  position: relative;\n  background-color: white;\n  width: min(90dvw, 700px);\n  min-height: 50%;\n  max-height: 70%;\n  display: flex;\n  flex-direction: column;\n  padding: 20px;\n  border-radius: 10px;\n  gap: 20px;\n}\nfor