dding: 60px 20px;\n    transform: translateX(-100%);\n    transition: transform 0.5s;\n  }\n  div.sidebar > button#x-button {\n    position: absolute;\n    top: 5px;\n    right: 5px;\n    width: 30px;\n    height: 30px;\n    background-color: red;\n    color: white;\n    display: grid;\n    place-items: center;\n    padding: 5px;\n    border-radius: 1px;\n  }\n  button.new-list-item-button {\n    z-index: 10;\n    position: fixed;\n    border-radius: 50%;\n    width: 70px;\n    height: 70px;\n    right: 10px;\n    bottom: 10px;\n    overflow: hidden;\n  }\n  button.new-list-item-button::after {\n    content: \"+\";\n    position: absolute;\n    inset: 0;\n    display: grid;\n    place-items: center;\n    font-size: 40px;\n  }\n  .todo-list {\n    padding-bottom: 100px;\n  }\n}\n"],"sourceRoot":""}]);
